import './App.css'
import './prototypes.js'
import {Route, BrowserRouter as Router, Routes} from "react-router-dom"
import Login from './pages/login'
import Home from './pages/homepage'
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import EditUserPhoto from './pages/edit-up'
import Buddies from './pages/buddies'
import Users from './pages/users'
import Install from './pages/install'
import Assignees from './pages/assignees'

import { GlobalProvider } from './context/global'
import UserAgent from './pages/test/user-agent/index.js'
import Catchup from './pages/catchup/index.js'

function App({auth}) {

  return (
    <div className="App">
      <>
        <GlobalProvider auth={auth}>
          <Router>
            <Routes>
              <Route path="/login" exact element={<Login />}></Route>
              <Route path="/edit-up" exact element={<EditUserPhoto/>}></Route>
              <Route path="/home" exact element={<Home/>}></Route>
              <Route path="/buddies" exact element={<Buddies/>}></Route>
              <Route path="/assignees" exact element={<Assignees/>}></Route>
              <Route path="/users" exact element={<Users/>}></Route>
              <Route path="/catchup" exact element={<Catchup/>}></Route>
              <Route path="/" exact element={<Install/>}></Route>

              <Route path="/test/user-agent" exact element={<UserAgent/>}></Route>
            </Routes>
          </Router>
        </GlobalProvider>
      </>
      <ToastContainer />
    </div>
  )
}

export default App
