const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
const phoneRegex = /^\d{10}$/

const statusColors = {
    regular: 'green',
    irregular: 'orange',
    inactive: 'red',
    new: 'blue',
    snoozed: 'grey'
}

const _ = {
    emailRegex,
    phoneRegex,
    statusColors
}

export default _