import "./index.css"
import DP from "../../../../components/dp"
import Swipe from "../../../../components/swipe"
import ContactBubble from "../../../../components/ContactBubble"
import Icon from "../../../../components/icon"
import { useGlobal } from "../../../../context/global"
import { useEffect, useState } from "react"
import PageLoader from "../../../../components/pageloader"

const FOLKMemberHome = ()=>{

    const { auth, api } = useGlobal()
    const { user } = auth

    var [ connectData, setConnectData ] = useState()
    var [ swipedConnect, setSwipedConnect ] = useState()

    useEffect(()=>{
        api.call('/get-home-data', user).then(res=>{
            setConnectData(res.data)
        })
    }, [])


    const clearSwipedConnect = ()=>{
      setSwipedConnect(null)
    }
  
    const handlePreacherConnect = ()=>{
      setSwipedConnect([connectData.preacherPhone, 'FOLK Guide'])
    }

    const handleBuddyConnect = ()=>{
        setSwipedConnect([connectData.buddyPhone, 'Buddy'])
      }

    return (
        connectData?<>
            {swipedConnect?
                <ContactBubble phone={swipedConnect[0]} name={swipedConnect[1]} close={clearSwipedConnect}/> 
            :null}

            {(connectData.buddyID || connectData.preacherID) && <div className='parthome-main'>

                <div className='parthome-head'>Get Connected..</div>

                {connectData.preacherID && <Swipe disabled width={"auto"} right={{minDrag:5, maxDrag: 20, onSwipe: handlePreacherConnect}}>
                    <div className='parthome-content'>
                        <div className="parthome-details-cont">
                            <DP size={[14, 6, 6]} user={{id: connectData.preacherID, name: connectData.preacherName}} enlarge badge={{color: "var(--bar)", icon: {name: "groups2", color: "white"}}}/>
                            <div className='parthome-details'>
                                <div className='parthome-title'>{"Your FOLK Guide"}</div>
                                <div className='parthome-name'>{connectData.preacherName}</div>
                            </div>
                        </div>
                        <Icon name="more-horiz" color="#888" onClick={handlePreacherConnect}/>
                    </div>
                </Swipe>}

                {connectData.buddyID && connectData.preacherID && <div className="parthome-sep"/>}

                {connectData.buddyID && <Swipe disabled width={"auto"} right={{minDrag:5, maxDrag: 20, onSwipe: handleBuddyConnect}}>
                    <div className='parthome-content'>
                        <div className="parthome-details-cont">
                            <DP size={[14, 6, 6]}  user={{id: connectData.buddyID, name: connectData.buddyName}} enlarge badge={{color: "var(--bar)", icon: {name: "group", color: "white"}}}/>
                            <div className='parthome-details'>
                                <div className='parthome-title'>{"Your FOLK Buddy"}</div>
                                <div className='parthome-name'>{connectData.buddyName}</div>
                            </div>
                        </div>
                        <Icon name="more-horiz" color="#888" onClick={handleBuddyConnect}/>
                    </div>
                </Swipe>}

                {false && <div className='parthome-note'>
                    <div>Swipe to contact</div>
                    <Icon name="keyboard-double-arrow-right" color="#aaa"/>
                </div>}

            </div>}   
        </>:<PageLoader text={"Loading home feed..."}/>
    )
}

export default FOLKMemberHome