import moment from "moment"

  const version = "1.0"
  
  const get=()=>{
    try {
      return JSON.parse(localStorage.getItem("save"))
    }catch{
      return null
    }
  }
  
  const set=(store)=>{
    try {
      localStorage.setItem("save", JSON.stringify({version, ...store}, null, 0), { expires: moment().add(100, 'years').toDate()})
      return true
    }catch{
      return false
    }
  }
  
  const clear=()=>{
    try {
      localStorage.removeItem("save")
      return true
    }catch{
      return false
    }
  }

  const reset = ()=> {
    clear()
    return window.location.pathname=='/login'?null:`/login?redirect=${window.location.pathname}`
  }
  
  const auth = ()=> {

    const openRoutes = [
      '/', '/test/user-agent'
    ]

    if(openRoutes.indexOf(window.location.pathname)!=-1){
      return
    }

    const store = get()
    if(!store || store.version != version){
      return reset()
    }

    if(window.location.pathname == '/login'){
      if(store){
        return new URLSearchParams(window.location.search).get('redirect') || '/home'
      }        
    }else{
      if(!store){
        return `/login?redirect=${window.location.pathname}`
      }
    }
  }
  
  const user=()=>{
    try {
      const store = JSON.parse(localStorage.getItem("save"))
      return store.users[store.index]
    }catch{
      return null
    }
  }

  const users=()=>{
    try {
      const store = JSON.parse(localStorage.getItem("save"))
      return store.users
    }catch{
      return null
    }
  }  

  const store = {
    get, set, clear, auth, user, users, version
  }

  export default store