import React, { createContext, useContext } from 'react'
import store from '../../store'
import API from '../../api'

const GlobalContext = createContext()

export const GlobalProvider = ({children})=>{

  const s = store.get()
  const auth = s?{
    store: s,
    user: s.users[s.index],
    users: s.users,
    index: s.index
  }:null

  const UAParser = require('ua-parser-js')
  const info = new UAParser().setUA(navigator.userAgent).getResult()

  const deviceInfo = {
    isMobileDevice: info.os.name=="Android" || info.os.name=="iOS",
    os: info.os.name,
    browser: info.browser.name,
    isChrome: info.browser.name=="Chrome",
    isDev: window.location.hostname == "localhost",
    appInstalled: !!window.matchMedia('(display-mode: standalone)').matches,
  }

  const api = new API()

  return (
    <GlobalContext.Provider value={{ auth, deviceInfo, api }}>
      {children}
    </GlobalContext.Provider>
  )
}

export const useGlobal = ()=>{
  return useContext(GlobalContext)
}
