import "./index.css"
import DP from '../../../components/dp';
import Icon from '../../../components/icon';
import Swipe from '../../../components/swipe';
import moment from 'moment';
import { useCallback, useState } from "react";
import _ from "../../../_";

const Buddy = ({buddy, onSwipe})=>{

  const handleSwipe = ()=>{
    onSwipe(buddy)
  }
  var [ showStats, setShowStats ] = useState()
  const toggleStats = useCallback(()=>{
    setShowStats(!showStats)
  }, [showStats])

  const stats = [
    {
      title: 'In FOLK since',
      value: buddy => moment(buddy.date).format('Do MMMM YYYY')
    },
    {
      title: 'Total sessions',
      value: buddy => `${buddy.attendance} sessions`
    },
    {
      title: 'Last Attended',
      value: buddy => {
        const weekCount = Math.floor(moment().diff(moment(buddy.lastAttended), 'days') / 7)
        return buddy.lastAttended?(moment().diff(moment(buddy.lastAttended), 'days') <= 7 ? `Last week session` : `${weekCount} week${weekCount>1?'s':''} ago`):'Never Attended'
      }
    },
    {
      title: 'Regularity',
      value: buddy => `${buddy.regularity} %`
    },
    {
      title: 'Status',
      value: buddy => buddy.status.toTitleCase()
    }
  ]

  return (
    <Swipe right={{color: _.statusColors[buddy.status], padWidth: 1, minDrag:10, maxDrag: 30, onSwipe: handleSwipe}}>
      <div className='buddy-box' key={buddy.name} onClick={toggleStats}>

        <div className='buddy-content'>
          <div className='buddy-details-cont'>
            <DP user={buddy} size={12} className={"buddy-dp"} enlarge/>

            <div className='buddy-details'>
              <div className='buddy-name'>{buddy.name}</div>
              <div className='buddy-preacher'>
                <Icon name="supervisor-account" size={5} color="#555"/>
                <div className='buddy-preacher-name'>{buddy.preacher}</div>
              </div>
            </div>
          </div>

          <div className='buddy-right-info'>
            <div className='buddy-course'>{buddy.courseID}</div>
            <div className='buddy-notif'>
              {
                buddy.dob && buddy.dob.dobDays()<=7?
                  <div className='buddy-notif-blob'>
                    <Icon className="buddy-notif-icon" name="cake" color="white" size={5.5}/>
                    <div>{moment(buddy.dob).format("DD MMM")}</div>
                  </div>
                :null
              }
              {
                buddy.snooze && buddy.snooze.snoozeDays()?
                    <div className='buddy-notif-blob'>
                    <Icon className="buddy-notif-icon" name="snooze" color="white" size={5.5}/>
                    <div>{moment(buddy.snooze).format("DD MMM")}</div>
                  </div>
                :null
              }
            </div>
          </div>
        </div>

        <div className={`buddy-stats ${showStats?'show':''}`}>
              {
                showStats?stats.map(stat => 
                  <div className="buddy-stat">
                    <div className="buddy-stat-title">{stat.title}</div>
                    <div className="buddy-stat-value">{stat.value(buddy)}</div>
                  </div>
                ):null
              }
        </div>

      </div>
    </Swipe>
  )
 
}


export default Buddy
