const Icon = (props)=>{

    var { name, color, size, style, onClick, className } = props

    if(!Array.isArray(size)){
        if(size){
            size = [size, size/2, size/3]
        }else{
            size = [7, 3.5, 2.333]
        }
    }

    style = style || {}
    className = className || ""
    style = { cursor: "pointer", ...style }

    return <StyledIcon
        className={className}
        onClick={onClick}
        style={style}
        fill={color || "black"}
        xmlns="http://www.w3.org/2000/svg"
        viewBox={icons[name].viewBox || "0 -960 960 960"}
        size={size}>
            <path d={icons[name].d || icons[name]}/>
        </StyledIcon>
}

export default Icon