import "./index.css"
import Icon from "../../icon"
import { useNavigate } from "react-router-dom"
import { useGlobal } from '../../../context/global'
import Glass from "../../glass"


const AppMenu = ({open, close})=>{

    const { auth } = useGlobal()
    const { user } = auth

    const navigate = useNavigate()

    const menus = [
        {
            name: "Home",
            link: "/home",
            icon: "home",
            access: 9
        },
        {
            name: "Users",
            link: "/users",
            icon: "group",
            access: 0
        },
        {
            name: "Buddies",
            link: "/buddies",
            icon: "group",
            access: 2,
            exact: true
        },
        {
            name: "Assignees",
            link: "/assignees",
            icon: "group",
            access: 1,
            exact: true
        }
    ]

    const socialMedia = [
        {
            name: "call",
            link: 'tel:+919945728008'
        },
        {
            name: "whatsapp",
            link: 'https://chat.whatsapp.com/GKGASC7J4v18JdCQJ628t4',
        },
        {
            name: 'instagram',
            link: 'https://www.instagram.com/folkmysore/'
        },
        {
            name: 'facebook',
            link: 'https://www.facebook.com/folkmys',
        },
        {
            name: 'youtube',
            link: 'https://www.youtube.com/channel/UCqkonVPtC6MZ3DWX0WB5ZEA'
        }
    ]

    return (
        <div>
            <div className={`appmenu-slider ${open?"open":""}`}>
                <div className="appmenu-cont">
                    <div className="appmenu-label">MENU</div>
                    <div className="appmenu-items">
                        {
                            menus.filter(m=>{
                                return m.exact?m.access==user.roleIndex: m.access >= user.roleIndex
                            }).map(m=>{
                                const handleMenuClick = ()=>{
                                    navigate(m.link)
                                }
                                return(
                                    <div className={`appmenu-item ${window.location.pathname==m.link?"selected":""}`} onClick={handleMenuClick}>
                                        <Icon name={m.icon} color="#fff" size={[5, 2.25, 1.75]}/>
                                        <div className="appmenu-item-name">{m.name}</div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
                
                <div>
                    <hr/>
                    <div className="appmenu-social">
                        {
                            socialMedia.map(s => <a href={s.link}>
                                <Icon
                                    name={s.name}
                                    color="white"
                                    size={[5,2.25,2]}
                                />
                            </a>)
                        }
                    </div>
                </div>
            </div>

            {open?<Glass onClick={close}/>:null}            
        </div>
    )
}

export default AppMenu