import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import './prototypes.js'
import store from './store';

const redirectionURL = store.auth()
if(redirectionURL){
  window.open(redirectionURL, "_self")
}else{
  ReactDOM.render(
    <React.StrictMode>
      <App/>
    </React.StrictMode>,
    document.getElementById('root')
  )
}

